.bg{
    position: fixed;
    height: 100%;
    width: 100%;
    background-image: url("./img/bg.jpg");
    background-attachment: fixed;
    background-clip: border-box;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    filter: blur(3px);
}